nav {
  background: rgba(63, 59, 59, 0.3);
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 100;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  gap: 0.8 rem;
  border-radius: 1rem 1rem 3rem 3rem;
  backdrop-filter: blur (15px);
}

nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-white);
  font-size: 1.1rem;
}

nav a:hover {
  background: rgba(0, 0, 0, 0.5);
}

nav a.active {
  background: var(--color-black);
  color: var(--color-white);
}
